.page-header__wrapper {
  // padding: 0 20px;
  z-index: 1;
}

.page-header__holder {
  display: flex;
  align-items: center;
  flex-direction: row;
  background-color: #64533e;
  padding: 10px 15px;
  gap: 70px;
}

.page-header__inner {
  width: 460px;
  display: flex;
  flex-direction: row;
  gap: 22px;
}

.page-header__button-holder-lang,
.page-header__email-holder,
.page-header__social-button {
  display: none;
}

.page-header__title-holder .page-header__title {
  margin: 0;
  font-weight: 400;
  font-size: 16px;
  color: white;
}

.page-header__title:nth-child(2) {
  color: #fdd099;
}

.page-header__nav-list {
  list-style: none;
}

.page-header__nav-item a {
  font-size: 14px;
  text-decoration: none;
  color: black;
}

.page-header__main-holder {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 10px;
}

.page-header__box {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 180px;
}

.page-header__main-wrap {
  padding: 70px 10px 10px 10px;
  display: flex;
  flex-direction: column;
  gap: 130px;
  justify-content: space-between;
}

.page-header__button {
  border: none;
  width: 162px;
  height: 47px;
  padding: 10px 40px;
  font-family: "Tahoma";
  font-weight: 400;
  font-size: 14px;
  background-color: #fdd099;
}

.page-header__title-holder .page-header__title {
  margin: 0;
  font-weight: 400;
  font-size: 20px;
}

.page-header__wrap {
  background-color: #64533e;
  display: flex;
  flex-direction: column;
}


.page-header__slogan {
  font-family: "Tahoma";
  font-weight: 400;
  font-size: 44px;
  height: 28px;
  color: white;
  margin: 0;
}


.page-header__test {
  font-family: "Tahoma";
  font-weight: 400;
  font-size: 18px;
  margin: 0;
  width: 428px;
  color: #fdd099;
}

.page-header__test br {
  display: block;
}

.page-header__button-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 30px;
}

.page-header__button-box {
  padding: 20px 10px;
}

.page-header__button-number-holder {
  padding: 0;
}



.page-header__text-button {
  border: none;
  width: 176px;
  height: 56px;
  padding: 5px;
  font-family: "Tahoma";
  font-weight: 400;
  font-size: 16px;
  background-color:#ed9438;
  color: white;
}

.page-header__button-number-holder {
  display: flex;
  flex-direction: row;
  gap: 2px;
  align-items: right;
  justify-content: right;
}

.page-header__button-number {
  height: 88px;
  width: 126px;
  border: none;
  font-family: "Tahoma";
  font-weight: 200;
  font-size: 66px;
  background-color: #fdd099;
}

.page-header__button-number:nth-child(1) {
  margin-top: 10px;
}

.page-header__button-number:nth-child(2),
.page-header__button-number:nth-child(3),
.page-header__button-number:nth-child(4) {
  height: 78px;
  width: 116px;
  margin-top: 20px;
  background-color: #60493b;
  color: grey;
}


.page-header__inner:nth-child(2) {
  display: flex;
  justify-content: right;
}





.page-header__navigation {
  width: 100%;
  background-color: white;
}

.navigation__link--menu {
  border: none;
  background: none;
  margin: 12px 0;
}


.page-header__navigation--closed .page-header__nav {
  display: none;
}

.page-header__navigation--opened .page-header__wrap {
  position: relative;
  top: -15px;
  right: 0;
  left: 0;
  min-height: 18vh;
  background-color: white;
  text-align: center;
}

.page-header__navigation--opened .page-header__nav::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  width: 7px;
  height: 100%;
}

.page-header__navigation--nojs .page-header__nav {
  position: static;
  min-height: 0;
}

.page-header__navigation--nojs .page-header__nav-item {
  display: block;
}


@media (min-width: $tablet-width) {
  .page-header__wrapper {
    width: 768px;
  }
}

@media (min-width: 1920px) {
  .page-header__wrapper {
    padding: 0;
  }
  .page-header__holder {
    padding: 55px 55px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    background-color: #64533e;
  }

  .page-header__holder {
    width: 1810px;
  }

  .page-header__button-holder-lang,
  .page-header__email-holder,
  .page-header__social-button {
    display: block;
  }

  .page-header__navigation--closed .page-header__nav {
    display: block;
  }

  .button-nav {
    display: none;
  }

  .page-header__inner {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    gap: 100px;
  }

  .page-header__title-holder .page-header__title {
