    color: #fdd099;
  }

  .page-header__wrap {
    background-color: #64533e;
    display: flex;
    flex-direction: row;
  }

  .page-header__nav-list {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    gap: 36px;
    background-color: white;
    list-style: none;
    margin: 0;
