  }

  .page-header__slogan {
    font-family: "Tahoma";
    font-weight: 400;
    font-size: 84px;
    height: 28px;
    color: white;
    margin: 0;
  }


  .page-header__test {
    font-family: "Tahoma";
    font-weight: 400;
