    padding: 10px 0 0px 220px;
  }

  .page-header__box {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 130px;
    height: 446px;
  }

  .page-header__inner:nth-child(2) {
    display: flex;
    justify-content: none;
  }


}
